import { useEffect, useState } from "react";
import AOS from 'aos';
import 'aos/dist/aos.css';



function About(){

    const[showMore, setShowMore]=useState(false);
    const text = "Willkommen bei ALL FOR YOU  Ihrem ultimativen Modegeschft fr stilvolle und hochwertige Bekleidung. Unser Ziel ist es, Ihnen eine breite Auswahl an modischen Kleidungsstcken und Accessoires anzubieten, die Ihren persnlichen Stil unterstreichen und Ihnen dabei helfen, sich selbstbewusst und schn zu fhlen. Unsere Geschichte beginnt im Jahr 2011, als unser Grnder, ein leidenschaftlicher Modedesigner mit einer Vision, beschloss, seine Leidenschaft fr Mode in ein florierendes Geschft umzuwandeln. Mit viel Hingabe und harter Arbeit gelang es ihm, ein kleines Modegeschft aufzubauen, das sich schnell einen Namen in der Branche machte. Heute sind wir stolz darauf, unseren Kunden eine beeindruckende Kollektion von Kleidungsstcken und Accessoires aus verschiedenen Teilen der Welt prsentieren zu knnen.\n\nBei ALL FOR YOU legen wir groen Wert auf Qualitt und Nachhaltigkeit. Deshalb arbeiten wir eng mit unseren Lieferanten zusammen, um sicherzustellen, dass unsere Produkte unter fairen Arbeitsbedingungen hergestellt werden und die Umwelt so wenig wie mglich belasten. Wir sind bestrebt, Ihnen die neuesten Trends aus der Modewelt anzubieten und gleichzeitig innovative Materialien und Produktionsmethoden zu nutzen.\n\nWir laden Sie herzlich ein, Teil unserer wachsenden Community von modebegeisterten Kunden zu werden! Folgen Sie uns auf Social Media oder abonnieren Sie unseren Newsletter fr exklusive Angebote sowie Neuigkeiten rund um unsere neuesten Kollektionen.\n\n Ihr Team von ALL FOR YOU";
    useEffect(()=>{
        AOS.init({duration:1000})
        }, [])
        
    
    
    
    return(
        
        <div className="containerabout" >
            <img className="aboutbild" data-aos="zoom-in" src="gal02.jpg" alt="Bild"/>
            <div className="abouttext" data-aos="zoom-in">
            <h3 className="abouttitel">All about us</h3>
               
                <p className="textabout" >{showMore ? text : text.substring(0,350) + "..."}<button className="btnabout" onClick={()=>setShowMore(!showMore)}>{showMore ? "Show less" : "Show more"}</button></p>

                </div>
        </div>
    )
}
export default About;